@font-face {
  font-family: "Forma Light";
  src: url("../static/forma-light.ttf");
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-family: "Forma Light";
  color: rgb(105, 105, 105);
  background-color: white;
}

.navbar-group {
  display: flex;
  flex-direction: row;
  width: 20%;
  justify-content: space-around;
  align-items: center;
  transition: all 0.3s ease-in;
}

.navbar-group > * {
  letter-spacing: 0.15em;

  display: inline-block;
  padding: 15px 20px;
  position: relative;
}
.navbar-group > *:hover {
  cursor: pointer;
  color: purple;
}

.navbar-group > *:after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: purple;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

.navbar-group > *:hover:after {
  width: 100%;
  left: 0;
}
